#skill {
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	padding: 30px;
}

#skill h2{
	font-size: 30px;
	padding: 30px 0 0 0;
}

#skill span{
	display: flex;
	justify-content: center;
	padding: 5px 0 20px 0;
	color: var(--dark-color);
	opacity: .7
}

.container-fluid{

    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: center;
    align-items: center;
}

.box{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-right: 10px;
	padding: 10px;

}

.box img{
	height: 100px;
	width: 100px;
	padding: 10px;
	
}

.box:hover img{
	animation: rotateImg 2.5s ease;
	cursor: pointer;
	
}

@keyframes rotateImg {
    0% {
        transform: rotate(0deg);
    }

    25% {
        transform: rotate(10deg);
    }

    50% {
        transform: rotate(-10deg);
    }
    100% {
        transform: rotate(0deg);
    }

}



