/*CONTACT*/

#contact{
	padding: 5rem 2rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: var(--primary-color);
}

.info{
	flex: 1;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: flex-start;
	flex-direction: column;
	padding: 15px 0;
}

.info h2{
	color: var(--white-color);
	padding: 20px 0;
	font-size: 30px; 
}

.info h3{
	color: var(--secondary-color);
	padding-bottom: 1rem;
}

.info p{
	color: var(--white-color);
	opacity: .85;
}

.fa-stack {
	margin-right: 0.2rem;
	margin-bottom: 0.65rem;
	margin-left: 0.12rem;
	width: 3rem;
	height: 3rem;
}

.fa-stack .circle {
	position: absolute;
	width: 3rem;
	height: 3rem;
	border-radius: 50%;
	transition: all 0.2s ease;
}

.fa-stack:hover .circle {
	background: white;
	
}

.fa-stack-1x {
	font-size: 1.5rem;
	line-height: 3.25rem;
	color: var(--white-color);
	transition: all 0.2s ease;
}

.fa-stack:hover .fa-stack-1x {
	color: var(--primary-color);
}

.info_form{
	flex: 1;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

form{
	display: flex;
	flex-direction: column;
    align-items: center;
    padding: 20px 0;
    opacity: 1;
    z-index: 1;

}

input{
	border: none;
	background-color: transparent; ;
	border-bottom: 2px solid #f0f8ff7a;
	outline: none;
	padding: 10px;
	border-radius: 2%;
	color: var(--white-color) ;	
}

input:hover,
textarea:hover {
	background-color: #f0f8ff7a ;
}

label{
	padding-top: 10px;
	color: #f0f8ff7a ;
}

textarea{
	background-color: transparent;
	min-width: 250px;
	max-height: 150px;
	max-width: 300px;
	border: none;
	border-bottom: 2px solid #f0f8ff7a;
	outline: none;
	padding: 10px;
	border-radius: 2%;
	color: var(--white-color) ;	
}